import React, { useState } from 'react';

export const Counter = () => {
   const [count, setCount] = useState(0);

   const number: number = 0;

   return (
      <div>
         <div>{count}</div>

         <button onClick={() => {
            if (count >= 10) {
               setCount(10)
            } else {
               setCount(count + 1)
            }
         }}>+</button>

         <button onClick={() => {
            if (count <= 0) {
               setCount(0)
            } else {
               setCount(count - 1)
            }
         }}>-</button>

         {count === 10 &&
            <p>10 is max value</p>
         }
      </div>
   );
};

